<template>
  <v-card
    v-resize="onResize"
    id="table"
    elevation="0"
    :class="{
      'slide-leave slide-enter-active': this.$store.state.sheet && hasDetail,
      'slide-leave-to slide-leave-active': !this.$store.state.sheet && hasDetail
    }"
    :style="getTableHeight"
  >
    <div
      class="v-data-table pl-2 v-data-table--dense v-data-table--fixed-height theme--light"
    >
      <div class="v-data-table__wrapper" :style="getTableHeight">
        <table class="v-data-table-body">
          <thead class="v-data-table-header">
            <tr>
              <th
                v-for="(h, i) in headers"
                :key="i"
                :class="getAlign(h, h.textMinify)"
                :rowspan="h.children ? 1 : 3"
                :colspan="getColspan(h)"
              >{{ h.text }}</th>
            </tr>
            <tr>
              <th
                v-for="(h1, i1) in getSubHeader(headers)"
                :key="i1"
                :class="getAlign(h1, h1.textMinify)"
                :rowspan="h1.children ? 1 : 2"
                :colspan="h1.children ? h1.children.length : 1"
              >{{ h1.text }}</th>
            </tr>
            <tr>
              <th
                v-for="(h2, i2) in getSubHeader(headers, true)"
                :key="i2"
                :class="getAlign(h2)"
                :rowspan="h2.children ? 1 : 2"
                :colspan="h2.children ? h2.children.length : 1"
              >{{ h2.text }}</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(item, i) in tableData"
              :key="i"
              @click="selectRow(i)"
              :class="{ 'selected-row': i === selectedItem }"
            >
              <td
                v-for="(header, col) in headersFlat"
                :key="col"
                :class="cellClasses[i + '_' + header.value]"
                @click="
                  openDetail(
                    $event,
                    item[header.value + '_request'],
                    header.value
                  ),
                    highlightCell(i, header, item)
                "
              >
                <v-tooltip
                  right
                  v-if="item[header.value + '_tooltip']"
                  max-width="190"
                  open-delay="2"
                  transition="fade-transition"
                >
                  <template v-slot:activator="{ on }">
                    <div
                      v-on="on"
                      v-if="
                        isEditable(item, header.editable, item[header.value])
                      "
                    >
                      <EditCell
                        @updateCell="
                          changeClass(
                            $event,
                            item[header.value],
                            item,
                            header,
                            i
                          )
                        "
                        :oldValue="item[header.value]"
                        :row="item"
                        :col="header"
                        :rowId="i"
                      />
                    </div>
                    <div v-on="on" v-else>
                      {{
                        item[header.value]
                          | customizeValue(
                            header.value,
                            item,
                            tableType,
                            tableData
                          )
                      }}
                    </div>
                  </template>
                  <span>{{
                    getTooltip(
                      item[header.value + '_tooltip'],
                      item[header.value]
                    )
                  }}</span>
                </v-tooltip>
                <template v-else>
                  <EditCell
                    @updateCell="
                      changeClass($event, item[header.value], item, header, i)
                    "
                    :oldValue="item[header.value]"
                    :row="item"
                    :col="header"
                    :rowId="i"
                    v-if="isEditable(item, header.editable, item[header.value])"
                  />
                  <div
                    v-else
                    :class="{
                      'detail-cell':
                        selectedCell.i === i &&
                        selectedCell.dataIndex === header.value
                    }"
                  >
                    {{
                      item[header.value]
                        | customizeValue(
                          header.value,
                          item,
                          tableType,
                          tableData
                        )
                    }}
                  </div>
                </template>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </v-card>
</template>

<script>
import axios from 'axios';
import EditCell from './EditCell';
export default {
  name: 'DataTable',
  components: {
    EditCell
  },
  created() {
    if (!this.isDetail)
      this.$store.state.defaultFiltersValues = Object.assign({}, this.params);
    this.fetchTableData(this.params);
    this.$store.state.sheet = false;
  },
  filters: {
    customizeValue(value, col, record, tableType, data) {
      let val = value;
      if (tableType === 'norm_depo' && col === 'sort_number') {
        if (record.type === '') {
          let ind = 0;
          data.every(function (rec) {
            if (rec.type === '') {
              ind++;
            }
            return record !== rec;
          });
          return ind;
        } else return '';
      }
      if (col === 'row_number') {
        //значения для простого нумерованного столбца
        let i = 0;
        data.every(function (rec) {
          i++;
          return record !== rec;
        });
        return i;
      }
      if (tableType === 'norm_ratio')
        if (typeof value === 'number') return Math.floor(val * 100) / 100;
      return val;
    }
  },
  computed: {
    getTableHeight() {
      const FILTERS_CONTENT_HEIGHT = 138,
        BLOCKS_HEIGHT = 210,
        DETAIL_HEIGHT = 65,
        OPEN_DETAIL_CONTENT = 190;
      let tableHeight = this.windowSize.y - BLOCKS_HEIGHT;

      if (this.hasDetail) tableHeight -= DETAIL_HEIGHT;

      if (this.$store.state.filterOpen) tableHeight -= FILTERS_CONTENT_HEIGHT;
      if (this.$store.state.sheet) tableHeight -= OPEN_DETAIL_CONTENT;

      if (this.isDetail) tableHeight = 185;

      return `height: ${tableHeight}px`;
    }
  },
  methods: {
    changeClass(val, oldVal, it, col, i) {
      this.setCellStyles(it, i, col, val);
      this.changeValue(val, col.value, i, oldVal);
      this.$store.state.cancelBtnActive = !this.editedValues.length > 0;
      this.$store.state.recalcBtnActive = !this.editedValues.length > 0;
      this.$forceUpdate();
    },
    changeValue(value, col, id, oldValue) {
      let isValueExist;
      this.editedValues.forEach((editedValue, i, arr) => {
        if (editedValue.id === id && editedValue.col === col) {
          if (+value !== +oldValue) {
            editedValue.value = value;
          } else {
            arr.splice(i);
          }
          isValueExist = true;
        }
      });
      if (!isValueExist) {
        this.editedValues.push({ value, col, id });
      }
    },
    highlightCell(i, col, it) {
      if (
        (it[col.value + '_request'] || col.value === 'duration') &&
        it.type !== 'GG' &&
        it.type !== 'SS' &&
        it.type !== 'G' &&
        it.type !== 'S' &&
        it.type !== 'I' &&
        (it[col.value] || it[col.value] === 0)
      ) {
        if (
          this.selectedCell.i === i &&
          this.selectedCell.dataIndex === col.value
        ) {
          this.selectedCell.i = null;
          this.selectedCell.dataIndex = null;
          this.$store.state.sheet = false;
        } else {
          this.selectedCell.i = i;
          this.selectedCell.dataIndex = col.value;
        }
      }
    },
    openDetail(e, url, key) {
      if (url || key === 'duration') {
        if (!this.$store.state.sheet) {
          setTimeout(
            () =>
              e.target.scrollIntoView({
                block: 'end',
                inline: 'nearest',
                behavior: 'smooth'
              }),
            50
          );
        }
        this.$store.state.sheet = true;
        this.$emit('loadDetail', url || '');
      }
    },
    selectRow(rowNumber) {
      this.selectedItem = rowNumber;
    },
    getAlign(header, info) {
      let cl = '';
      if (info === 'mini') {
        cl += 'smaller-text ';
      }
      if (header.border) {
        cl += 'thick-border-bottom ';
      } else cl += 'thin-border-bottom ';
      if (!header.alignHeader) {
        cl += 'align-text-left';
      }
      return cl;
    },
    getTooltip(tooltipText, value) {
      if (tooltipText) {
        if (
          this.tableType === 'norm_sec' ||
          this.tableType === 'reg_join' ||
          this.tableType === 'reg_sec'
        ) {
          let ODZ = tooltipText.split(';'),
            minODZ = Number(ODZ[0].replace(/\D+/g, '')),
            maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
            originValue,
            approveUser = '';

          if (Number(ODZ[2].replace(/\D+/g, '')))
            originValue = Number(ODZ[2].replace(/\D+/g, ''));
          if (ODZ[3].split(' = ')[1] !== '')
            approveUser = ODZ[3].split(' = ')[1];

          if (ODZ) {
            if (approveUser && originValue && value !== originValue) {
              return (
                'Данные изменены: ' +
                approveUser +
                ' с ' +
                originValue +
                ' на ' +
                value +
                '; мин = ' +
                minODZ +
                '; макс = ' +
                maxODZ
              );
            } else {
              return 'мин = ' + minODZ + '; макс = ' + maxODZ;
            }
          } else {
            return 'мин = ' + minODZ + '; макс = ' + maxODZ;
          }
        }
        if (this.tableType === 'norm_depo') {
          return tooltipText;
        }
      } else return '';
    },
    isEditable(it, ed, value) {
      return (
        value !== undefined &&
        ed &&
        it.type !== 'S' &&
        it.type !== 'SS' &&
        it.type !== 'I'
      );
    },
    setCellStyles(it, ind, col, newValue) {
      let cl_string = '';
      if (+it[col.value] || +it[col.value] === 0) {
        cl_string += 'numbers-font-size ';
      }
      if (
        (col.editable ||
          it[col.value + '_request'] ||
          col.value === 'duration') &&
        it.type !== 'GG' &&
        it.type !== 'SS' &&
        it.type !== 'G' &&
        it.type !== 'S' &&
        it.type !== 'I' &&
        (it[col.value] || +it[col.value] === 0)
      ) {
        if (it[col.value + '_request'] || col.value === 'duration') {
          cl_string += 'detail-cell-main ';
        } else {
          if (newValue && +newValue !== +it[col.value]) {
            cl_string += 'edited-cell-main ';
          } else cl_string += 'edit-cell-main ';
        }
      }
      if (this.tableType === 'reg_join') {
        if (col.editable && it[col.value] === 0) cl_string += 'back-pink ';
      }
      if (
        it.type === 'GG' ||
        it.type === 'SS' ||
        it.type === 'G' ||
        it.type === 'S' ||
        it.type === 'I'
      )
        cl_string += 'bold-text ';
      if (col.fixedColumn) cl_string += 'fixed-border ';
      if (it[col.value + '_color']) {
        if (it[col.value + '_color'] === 'yellow') cl_string += 'back-yellow ';
      }
      if (it[col.value + '_tooltip'] && this.tableType !== 'norm_depo') {
        let ODZ = it[col.value + '_tooltip'].split(';'),
          minODZ = Number(ODZ[0].replace(/\D+/g, '')),
          maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
          maxODZ10 = Math.ceil(maxODZ * 1.1),
          originValue,
          approveUser = '',
          value = newValue ? newValue : it[col.value];

        if (Number(ODZ[2].replace(/\D+/g, '')))
          originValue = Number(ODZ[2].replace(/\D+/g, ''));
        if (ODZ[3].split(' = ')[1] !== '') approveUser = ODZ[3].split(' = ')[1];

        if (ODZ) {
          if (
            approveUser &&
            (originValue || originValue === 0) &&
            value !== originValue
          ) {
            if (
              !(value >= minODZ && value <= maxODZ) &&
              (value === 0 || value > maxODZ10)
            ) {
              cl_string += 'back-pink edited-cell-main ';
            } else if (
              !(value >= minODZ && value <= maxODZ) &&
              (value === 0 || value > maxODZ10)
            ) {
              cl_string += 'back-pink ';
            } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
              cl_string += 'back-yellow edited-cell-main ';
            }
          } else {
            if (
              !(value >= minODZ && value <= maxODZ) &&
              (value === 0 || value > maxODZ10)
            ) {
              cl_string += 'back-pink edit-cell-main ';
            } else if (
              !(value >= minODZ && value <= maxODZ) &&
              (value === 0 || value > maxODZ10)
            ) {
              cl_string += 'back-pink ';
            } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
              cl_string += 'back-yellow edit-cell-main ';
            }
          }
        }
      }
      this.cellClasses[ind + '_' + col.value] = cl_string;
    },
    fetchTableData(params) {
      if (!this.isDetail || (this.isDetail && this.$store.state.sheet)) {
        axios
          .get(this.url || params, {
            params: !this.isDetail ? params : ''
          })
          .then((response) => {
            this.tableData = response.data.data;
            this.tableData.forEach((row, i) => {
              this.headersFlat.forEach((col) => {
                this.setCellStyles(row, i, col);
              });
            });
          });
      }
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    getSubHeader(headers, deeper) {
      let result = [];
      headers
        .filter((i) => i.children)
        .forEach((v) => {
          result = result.concat(v.children);
        });
      if (deeper) {
        let subChild = [];
        result
          .filter((l) => l.children)
          .forEach((b) => {
            subChild = subChild.concat(b.children);
          });
        return subChild;
      }
      return result;
    },
    getColspan(col) {
      let colspan;
      if (col.cols) {
        colspan = col.cols;
      } else colspan = col.children ? col.children.length : 1;
      return colspan;
    }
  },
  props: [
    'headers',
    'headersFlat',
    'url',
    'params',
    'tableType',
    'isDetail',
    'hasDetail'
  ],
  data() {
    return {
      editedValues: [],
      selectedItem: null,
      cellClasses: {},
      selectedCell: {
        i: null,
        dataIndex: null
      },
      windowSize: {
        x: 0,
        y: 0
      },
      tableData: []
    };
  }
};
</script>

<style scoped lang="sass">
#table
  margin-bottom: 4px
  overflow: auto
  .v-data-table__wrapper
    overflow: auto
  .v-data-table-header
    position: sticky
    top: 0
    background-color: $color-white
    z-index: 2
  .v-data-table
    .v-data-table-body
      border-spacing: 0
    tbody
      tr.selected-row,
      tr:hover
        background-color: $color-light-blue
    td
      position: relative
      z-index: 1
      padding-left: 10px
      padding-right: 2px
      color: $color-dark-blue
      font-size: 12px
      height: 32px
      line-height: 14px
      border-color: $color-light-grey
      border-style: solid
      border-left-width: 0
      border-right-width: 0
      border-top-width: 0
      border-bottom-width: 1px
      div.detail-cell::after
        display: block
        position: absolute
        content: ''
        top: 7%
        left: 7%
        height: 85%
        width: 85%
        z-index: 10
        border: 1px solid $color-blue-links
        border-radius: $common-radius
        cursor: pointer
    td.numbers-font-size
      font-size: 14px
      line-height: 16px
    td.back-pink
      background-color: $color-pink
    td.back-yellow
      background-color: $color-light-yellow
    td.edit-cell-main
      min-width: 50px
    td.edit-cell-main::before,
    td.edited-cell-main::before
      font-family: $custom-icon-font
      color: $edit-triangle-blue
      content: "\e94f"
      position: absolute
      font-size: 8px
      top: -4px
      left: 0
    td.edited-cell-main::before
      color: $color-blue-links
    td.detail-cell-main
      cursor: pointer
      color: $color-blue-links
    td.bold-text
      font-family: $font-bold
    td.fixed-border
      border-right-width: 1px
    td div
      margin-top: 3px
    th
      border-color: $color-light-grey
      white-space: pre
      font-size: $font-size-root
      line-height: $line-height-root
      font-weight: normal
      color: $color-dark-blue
      border-style: solid
      border-left-width: 0
      border-right-width: 1px
      border-top-width: 0
      position: sticky
      padding: 8px 4px
    th.align-text-left
      text-align: left
    th.smaller-text
      font-size: 12px
      line-height: 14px
  tbody tr:nth-of-type(even)
    background-color: rgba(210, 218, 231, 0.25)

.v-sheet.v-card
  border-radius: 0 0 $common-radius $common-radius
.thick-border-bottom
  border-bottom-width: 3px
.thin-border-bottom
  border-bottom-width: 1px
.theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th
  border: none

.v-text-field
  margin: 0
  font-size: 14px
  ::v-deep
    .v-input__slot
      padding: 0
      &::before
        border-style: none !important
      input
        color: $color-dark-blue
        .edit-cell-main &,
        .edited-cell-main &
          color: $color-blue-links
</style>
