<template>
  <div class="pa-5 pb-0">
    <Filters :filters="{ dateFilter: true }" @reloadTable="updateTable()" />
    <Buttons :params="params" />
    <div class="text-info">
      План работы технических станций Восточный полигон на
      <span class="text-info-bold">{{ getDate }}</span>
    </div>
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import Filters from '../components/Filters';
import Buttons from '../components/Buttons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';

export default {
  name: 'PlanningTechStationTasks',
  components: { Buttons, Filters, Tabs, DataTable },
  computed: {
    getDate() {
      const [year, month, day] = this.$store.state.filtersValues.date.split(
        '-'
      );
      return `${day}.${month}.${year}`;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      url: '',
      tableType: 'st_tasks',
      params: {
        buttons: {
          print: true
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 0,
      headersFlat: [],
      headers: []
    };
  }
};
</script>

